<template>
  <div class="study2">
    <h1>{{title}}</h1>
    <section class="study2-ls" v-html="content"></section>
  </div>
</template>

<script>
import { $fetch } from '@/store'

const DOMAIN = 'https://jrainlau.com/api/study/articles/detail'

export default {
  data () {
    return {
      href: '',
      title: '',
      content: ''
    }
  },
  mounted () {
    document.body.querySelector('header').style.display = 'none'
    document.title = 'Study hard'
    const { href, title } = this.$route.query
    this.href = href.replace(/^[/](.*)[/]$/g, '$1')
    this.title = title
    this.getDetail()
  },
  methods: {
    async getDetail () {
      const result = await $fetch({
        url: DOMAIN,
        method: 'post',
        data: {
          domain: localStorage.getItem('study_keyword'),
          href: this.href
        }
      }).catch(e => e.response)
      this.content = result.data.replace(/<script.*?>.*?<\/script>/ig, '')
    }
  }
}
</script>

