@import '../assets/style/variables.less';
.study2 {
  height: 100%;
  width: 100%;
  overflow: scroll;
  h1 {
    padding: @gapInner;
    padding-bottom: 0;
    margin: 0;
  }
  &-ls {
    width: 100%;
    padding: @gapInner;
    box-sizing: border-box;
    line-height: 1.5;
    color: @regularFontColor;
    font-size: 1.2rem;
  }
}
</style>
